.container {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 10px;
}

.header {
  background-color: rgb(118, 118, 252);
  height: 100vh;
  color: #fff;
  font-family: "Roboto", sans-serif;
}

.header__title {
  text-align: center;
  padding-top: 40px;
  margin-bottom: 50px;
}

.location {
  font-weight: 700;
}

.today {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  margin: 10px 30px;
  align-items: center;
  font-weight: 700;
}

.header__content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.search {
  text-align: center;
}

.search-input {
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.781);
  color: #fff;
  padding: 20px 80px;
  text-align: center;
  font-size: 20px;
}

.search-input::placeholder {
  color: #fff;
  outline: none;
  transition: all 0.3s;
}

.search-input:focus::placeholder {
  color: transparent;
}

.search-input:focus {
  outline: none;
}

.first__block {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.weaher__icon,
.location,
.temp {
  text-align: center;
  flex: 33%;
}

.weather {
  font-weight: 700;
}

.temperature {
  font-size: 30px;
  font-weight: 700;
}

.feels {
  font-size: 18px;
}

.second__block {
  padding-top: 10px;
  border-top: 1px solid rgba(255, 255, 255, 0.781);
  margin-top: 30px;
  display: flex;
  width: 80%;
  align-items: center;
  justify-content: center;
  gap: 40px;
}

.count {
  text-align: center;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 20px;
}

@media (max-width: 425px) {
  .header__title {
    margin-bottom: 30px;
  }
  .search-input {
    background-color: transparent;
    border: 1px solid #fff;
    color: #fff;
    width: 90%;
    text-align: center;
    font-size: 20px;
    padding: 20px 50px;
  }
  .feels {
    display: flex;
    flex-direction: column;
  }
  .location {
    margin-bottom: 15px;
  }

  .second__block {
    width: 95%;
  }

  .today {
      margin: 20px;
  } 
}


